import { Fab } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import React from 'react';

const useStyles = makeStyles((theme) => ({
    linkedIn: {
        backgroundColor: '#247bb5',
        color: '#fff',
        margin: theme.spacing(1)
    },facebook: {
        color: '#fff',
        backgroundColor: '#3b5998',
        margin: theme.spacing(1)
      },twitter: {
        backgroundColor: '#55acee',
        color: '#fff',
        margin: theme.spacing(1)
      },gmail: {
        backgroundColor: '#dd4b39',
        color: '#fff',
        margin: theme.spacing(1)
      },
  }));



export default function SocialIcon(props){
    const classes = useStyles();
    const renderSocialIcon = (type,url,iconType) => {
        if(type==='facebook'){
            return (
            <Fab size="small" variant={iconType} className={classes.facebook}>
                <i class="fab fa-facebook-f"></i>
            </Fab>
            )  
        } else if(type==='twitter'){
            return (
            <Fab size="small" variant={iconType} className={classes.twitter}>
                <i class="fab fa-twitter"></i>
            </Fab>
            )   
        } else if(type==='gmail'){
            return (
            <Fab size="small" variant={iconType} className={classes.gmail}>
                <i class="fab fa-google"></i>
            </Fab>
            )   
        }  else if(type==='linkedIn'){
            return (
            <Fab size="small" variant={iconType} className={classes.linkedIn}>
                <i class="fab fa-linkedin-in"></i>
            </Fab>
            )   
        } 
    }
    return renderSocialIcon(props.type,props.url,props.iconType);
}